.credo-container {
    max-width: 1300px;
    min-height: 100vh;
    margin: 0 auto;
    padding: 60px 20px;
    color: var(--light-gray);
    background-color: var(--primary-color);

}

/* --- HEADER --- */
.credo-header {
    text-align: center;
    margin-bottom: 60px;
}

.credo-pretitle {
    text-transform: uppercase;
    letter-spacing: 3px;
    font-size: 0.9rem;
    color: var(--light-gray);

    display: block;
    margin-bottom: 10px;
}

.credo-header h1 {
    font-size: 3rem;
    color: var(--light-gray);

    margin: 0;
    font-family: 'Georgia', serif;
    /* Una fuente con serifa da más autoridad/tradición */
}

.header-divider {
    width: 60px;
    height: 4px;
    background-color: var(--light-gray);
    /* Un toque de rojo o dorado queda bien */
    margin: 20px auto;
    border-radius: 2px;
}

.credo-intro {
    max-width: 600px;
    margin: 0 auto;
    font-size: 1.1rem;
    color: var(--light-gray);

    line-height: 1.6;
}

/* --- GRILLA DE VALORES --- */
.credo-grid {
    display: grid;
    /* Crea columnas automáticas de mínimo 300px */
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 30px;
}

.credo-card {
    background: #fdfdfd;
    border: 1px solid var(--border-color);
    padding: 30px;
    border-radius: 8px;
    position: relative;
    transition: all 0.3s ease;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

/* Efecto al pasar el mouse: se levanta un poco */
.credo-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 15px 30px rgba(0, 0, 0, 0.08);
    border-color: var(--primary-color);
}

.card-number {
    position: absolute;
    top: 15px;
    right: 20px;
    font-size: 2.5rem;
    font-weight: bold;
    color: var(--text-color);
    /* Muy clarito, de fondo */
    z-index: 0;
}

.card-icon {
    font-size: 3rem;
    margin-bottom: 20px;
    z-index: 1;
    color: var(--primary-color);
    /* Para que quede arriba del número */
}

.card-text {
    font-size: 1.2rem;
    color: var(--text-color);
    font-weight: 500;
    line-height: 1.5;
    z-index: 1;
}

/* --- FOOTER DECORATIVO --- */
.credo-footer {
    text-align: center;
    margin-top: 60px;
    font-family: 'Georgia', serif;
    font-style: italic;
    color: var(--light-gray);

    font-size: 1.2rem;
}

/* Ajuste Mobile */
@media (max-width: 768px) {
    .credo-header h1 {
        font-size: 2rem;
    }

    .credo-grid {
        grid-template-columns: 1fr;
        /* Una sola columna en celular */
    }
}