:root {
    --primary-color: #48111d;
    /* Color principal corporativo */
    --secondary-color: #f39c12;
    /* Color secundario (acciones / contraste) */
    --text-color: #333333;
    /* Texto principal */
    --light-gray: #f8f9fa;
    /* Fondo claro / secciones */
    --border-color: #e0e0e0;
    /* Bordes suaves */
    --font-main: 'Helvetica Neue', Helvetica, Arial, sans-serif;
    /* Fuente base */
}

.about-container {
    max-width: 1300px;
    min-height: 100vh;
    margin: 0 auto;
    padding: 40px 20px;
    background-color: var(--primary-color);
    color: var(--text-color);

}

/* Header */
.about-header {
    text-align: center;
    margin-bottom: 40px;
}

.about-header h1 {
    font-size: 3rem;
    color: var(--secondary-color);

    margin-bottom: 10px;
}

.about-subtitle {
    font-size: 1.2rem;
    color: var(--light-gray);

    font-style: italic;
}

/* Video Responsive (Mantiene proporción 16:9 siempre) */
.video-responsive {
    position: relative;
    padding-bottom: 56.25%;
    /* 16:9 ratio */
    height: 0;
    overflow: hidden;
    border-radius: 12px;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
    margin-bottom: 60px;
}

.video-responsive iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

/* Secciones Generales */
.about-section {
    margin-bottom: 80px;
}

.about-section h2 {
    font-size: 2rem;
    color: var(--secondary-color);

    margin-bottom: 20px;
    text-align: center;
}

.lead-text {
    text-align: center;
    font-size: 1.1rem;
    line-height: 1.8;
    max-width: 800px;
    margin: 0 auto 50px auto;
    color: var(--light-gray);

}

/* Grid de Animales */
.animals-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 30px;
    margin-bottom: 60px;
}

.animal-card {
    background: #f9f9f9;
    padding: 30px;
    border-radius: 10px;
    text-align: center;
    border: 1px solid #eee;
    transition: transform 0.3s;
    color: var(--text-color);
}

.animal-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
    border-color: var(--primary-color);
}

.animal-card .icon {
    font-size: 3rem;
    margin-bottom: 15px;
    color: var(--primary-color);
    display: flex;
    justify-content: center;
    align-items: center;
}

.animal-card h3 {
    margin: 10px 0;
    font-size: 1.3rem;
}

/* Linea de Tiempo (Proceso) */
.process-timeline {
    text-align: center;
    padding: 40px;
    background-color: #f0f4f8;
    max-width: 50vw;
    margin: 0 auto;
    /* Un azul muy clarito */
    border-radius: 15px;
}

.steps-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    gap: 20px;
    margin-top: 30px;
}

.step {
    font-weight: bold;
    color: var(--text-color);
    font-size: 0.9rem;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.step svg {
    color: var(--primary-color);
    margin-bottom: 5px;
}

.arrow {
    color: #999;
    font-size: 1.5rem;
}

@media (max-width: 768px) {
    .arrow {
        display: none;
    }

    /* Ocultar flechas en móvil para que se apilen limpio */
    .steps-container {
        flex-direction: column;
        gap: 30px;
    }
}

/* Sección Empresa Familiar */
.family-section {
    background-color: var(--primary-color);
    color: white;
    padding: 60px 40px;
    border-radius: 12px;
    text-align: center;
}

.family-section h2 {
    color: white;
}

.family-tagline {
    font-size: 1.2rem;
    text-transform: uppercase;
    letter-spacing: 2px;
    opacity: 0.8;
    margin-bottom: 30px;
}

.family-content p {
    font-size: 1.1rem;
    line-height: 1.8;
    max-width: 700px;
    margin: 0 auto 40px auto;
}

.btn-about-cta {
    display: inline-block;
    padding: 12px 30px;
    background-color: white;
    color: var(--primary-color);
    text-decoration: none;
    font-weight: bold;
    border-radius: 5px;
    transition: background 0.2s;
}

.btn-about-cta:hover {
    background-color: #eee;
}