/* Dark Theme for Product CRUD */
.product-crud-container {
    background-color: transparent;
    color: var(--light-color);
}

/* Formulario Limpio (Dark) */
.product-crud-form {
    background-color: var(--primary-color);
    padding: 30px;
    border-radius: 8px;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3);
    margin-bottom: 30px;
    border: 1px solid var(--border-color);
}

.product-crud-form h4 {
    color: var(--secondary-color);
    margin-top: 0;
    margin-bottom: 25px;
    padding-bottom: 10px;
    border-bottom: 1px solid var(--border-color);
    font-size: 1.25rem;
}

/* Campos de Formulario */
.product-crud-form .form-group {
    margin-bottom: 20px;
}

.product-crud-form label {
    display: block;
    margin-bottom: 8px;
    color: var(--light-color);
    font-weight: 600;
    font-size: 0.9rem;
}

.product-crud-form input,
.product-crud-form select,
.product-crud-form textarea {
    width: 100%;
    padding: 10px 12px;
    border: 1px solid var(--border-color);
    border-radius: 6px;
    font-size: 1rem;
    background-color: var(--primary-color);
    color: var(--light-color);
    transition: border-color 0.2s, box-shadow 0.2s;
}

.product-crud-form input:focus,
.product-crud-form select:focus,
.product-crud-form textarea:focus {
    border-color: var(--secondary-color);
    outline: none;
    box-shadow: 0 0 0 3px rgba(243, 156, 18, 0.2);
}

/* Botones */
.btn-primary {
    background-color: var(--secondary-color);
    color: var(--text-color);
    padding: 10px 20px;
    border: none;
    border-radius: 6px;
    font-weight: 600;
    cursor: pointer;
    transition: background 0.2s;
}

.btn-primary:hover {
    filter: brightness(1.1);
}

.btn-secondary {
    background-color: transparent;
    border: 1px solid var(--border-color);
    color: var(--light-color);
    padding: 10px 20px;
    border-radius: 6px;
    cursor: pointer;
    margin-left: 10px;
    transition: all 0.2s;
}

.btn-secondary:hover {
    background-color: rgba(255, 255, 255, 0.1);
    color: var(--light-color);
}

/* Listado de Productos (Grid o Tabla) */
.product-crud-table {
    width: 100%;
    border-collapse: separate;
    border-spacing: 0;
    background: var(--primary-color);
    border-radius: 8px;
    overflow: hidden;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3);
    border: 1px solid var(--border-color);
}

.product-crud-table th {
    background-color: var(--primary-color);
    color: var(--secondary-color);
    font-weight: 700;
    text-transform: uppercase;
    font-size: 0.85rem;
    padding: 15px;
    border-bottom: 2px solid var(--border-color);
    text-align: left;
}

.product-crud-table td {
    padding: 15px;
    border-bottom: 1px solid var(--border-color);
    color: var(--light-color);
    vertical-align: middle;
}

.product-crud-table tr:last-child td {
    border-bottom: none;
}

.product-crud-table tr:hover {
    background-color: rgba(255, 255, 255, 0.05);
}

/* Acciones en tabla */
.btn-icon {
    border: none;
    padding: 6px 12px;
    border-radius: 4px;
    cursor: pointer;
    margin-right: 5px;
    font-size: 0.85rem;
    font-weight: bold;
    background-color: var(--secondary-color);
    color: var(--text-color);
}

.btn-icon.delete {
    background-color: #8b1a25;
    color: white;
    border: 1px solid #8b1a25;
}